import { Modal } from "../Modal/Modal";
import "./Search.css"

export const Search = ({ setFilterValue, filterValue, nameEdit, setNameEdit, setPriceEdit, priceEdit, handleAdd, modalAddActive, setModalAddActive}) => {

    const openModal = () => {
        setNameEdit('')
        setPriceEdit('')
        setModalAddActive(true)
    }

    return (
        <div className="search__container">
            <img
                alt="plus"
                src="data:image/png;base64,"
                className="modal__add__img"
                onClick={() => openModal()}
            />
            <label htmlFor="">
                Шукати:
                <input
                    className="input__search"
                    type='search'
                    value={filterValue}
                    onChange={(e) => setFilterValue(e.target.value)}
                />
            </label>
            <Modal setActive={setModalAddActive} active={modalAddActive}>
                <h2>You can add goods!</h2>
                <form className="form" method="get" onSubmit={(e) => handleAdd(e)}>
                    <label >
                        Name:
                        <textarea className="form__item" value={nameEdit} onChange={(e) => setNameEdit(e.target.value)} />
                    </label>
                    <label>
                        Price:
                        <input className="form__item" value={priceEdit} onChange={(e) => setPriceEdit(e.target.value)} />
                    </label>
                    <button type="submit" className="form__button">Add</button>
                </form>
            </Modal>
        </div>
    );
};